import { HttpClient } from '@angular/common/http';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Socket } from 'ngx-socket-io';
import { environment } from '../environments/environment';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  title = 'test-project';
  data = { lat: 10.3157, lng: 123.8854 };
  zoom = 14;
  kmlUrl = 'https://files-server.tk/0:/Server%20Downloads/mandauekml.kml';
  mapTypeId = 'satellite';

  activeDevices: { id: string, lat: number, lng: number }[] = [];
geoJsonObject: any;

  constructor(private socket: Socket,
    private changeRef: ChangeDetectorRef,
    private http: HttpClient,
  ) {

  }

  ngOnInit(): void {
    this.http.get(`${environment.socketUrl}/api/connected-device`)
      .subscribe((res: any) => {
        this.activeDevices = res;
        this.changeRef.detectChanges();
      });

    this.socket.on('coordinates/room', (res) => {
      const data = JSON.parse(res);
      const idx = this.activeDevices.findIndex(d => d.id === data.deviceId);
      if (idx > -1) {
        this.activeDevices[idx].lat = data.latitude;
        this.activeDevices[idx].lng = data.longitude;
      } else {
        this.activeDevices.push({
          id: data.deviceId,
          lat: data.latitude,
          lng: data.longitude
        });
      }
      this.changeRef.detectChanges();
    });
  }
}
